.grid {
  width: 100vw;
  padding-top: 6rem;
}
.grid-content {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 3rem;
}
.grid-info-1 {
  width: 40%;
  margin-left: 0%;
}
.grid-info-2 {
  width: 40%;
  margin-left: 10%;
}
.grid-info-3 {
  width: 40%;
  margin-left: 50%;
}
.grid-info-4 {
  width: 40%;
  margin-left: 60%;
}
@media screen and (max-width: 767px) {
  .grid-content {
    padding: 0 2rem;
  }
  .grid-info-1 {
    width: 90%;
    margin-left: 0;
  }
  .grid-info-2 {
    width: 90%;
    margin-left: 0;
  }
  .grid-info-3 {
    width: 90%;
    margin-left: 10%;
  }
  .grid-info-4 {
    width: 90%;
    margin-left: 10%;
  }
}
